import React from 'react'
import { useNavigate} from 'react-router-dom'
import { useContext } from 'react';
import BlockChainContext from '../Context/blockchain/blockchainContext';



export default function Details() {

  const { user } = useContext(BlockChainContext);
  const Navigate = useNavigate();

  function HandleClick(event) {

      event.preventDefault();
      Navigate('/generatekeys');


  }
  
  return (
    <div className="container px-5 py-24 mx-auto">
    <div className="text-center mb-20">
      <h1 className="sm:text-3xl text-2xl font-medium text-center title-font text-gray-900 mb-4">Your Voter Details as per Election Data</h1>
      <p className="text-base leading-relaxed xl:w-2/4 lg:w-3/4 mx-auto">Please confirm and check before Voting</p>
    </div>
    <div className="flex flex-wrap lg:w-4/5 sm:mx-auto sm:mb-2 -mx-2">
      <div className="p-2 sm:w-1/2 w-full">
        <div className="bg-gray-100 rounded flex p-4 h-full items-center">
        <i class='bx bxs-user px-3 text-green-500 bx-sm'></i>
          <span className="title-font font-medium">{user.name}</span>
        </div>
      </div>
      <div className="p-2 sm:w-1/2 w-full">
        <div className="bg-gray-100 rounded flex p-4 h-full items-center">
        <i className="bx bxs-id-card px-3 text-green-500 bx-sm"></i>
          <span className="title-font font-medium">{user.cnic}</span>
        </div>
      </div>
      <div className="p-2 sm:w-1/2 w-full">
        <div className="bg-gray-100 rounded flex p-4 h-full items-center">
        <i className="bx bxs-home-circle px-3 text-green-500 bx-sm"></i>
          <span className="title-font font-medium">{user.address}</span>
        </div>
      </div>
      <div className="p-2 sm:w-1/2 w-full">
        <div className="bg-gray-100 rounded flex p-4 h-full items-center">
        <div className='px-3 text-green-500'>
         {user.isValid === true ? <i class='bx bxs-user-check bx-sm'></i> : <i class='bx bxs-user-x bx-sm' ></i>}
        </div>
          <span className="title-font font-medium">{user.isValid === true ? "Verified" : "Not Verified"}</span>
        </div>
      </div>
    </div>
    <div class="flex justify-between">
    <button onClick={(event)=> { event.preventDefault(); Navigate('/Vote') }} className="flex mx-auto  text-white bg-green-500 border-0 py-2 px-8 focus:outline-none hover:bg-green-600 rounded text-lg">Vote</button>
    <button onClick={HandleClick} className="flex mx-auto text-white bg-green-500 border-0 py-2 px-8 focus:outline-none hover:bg-green-600 rounded text-lg">Generate Keys</button>
    </div>
  </div>
  )
}
