import { useState } from "react";
import UserInterfaceContext from "./uicontext";


function UserInterfaceState(props) {
 

  const [alert, setAlert] = useState(null);


  function ShowAlert(message, type) {

     setAlert({
       
       msg : message,
       type : type
     })





    }

     
    return (
            <div>
              <UserInterfaceContext.Provider value={{ alert, setAlert, ShowAlert }}>
                {props.children}
              </UserInterfaceContext.Provider>
            </div>
          )



    
}


export default UserInterfaceState
