import { useContext, useState } from 'react'
import BlockChainContext from '../Context/blockchain/blockchainContext'

import { useNavigate } from 'react-router-dom';


export default function Input(props) {


  const navigate = useNavigate();
  const { setUrl } = useContext(BlockChainContext);
  const [input, setInput] = useState('');


  function handleChange(event){

     setInput(event.target.value);

  }
 
  function handleSubmit(event)
  {
     event.preventDefault();
     if(input.length < 13)
     {
        alert("Not a valid input")
     }
     else {

      setUrl(input);
      navigate('/details')
     }


  }

  return (
    <div className="flex items-center justify-center h-screen">
      <div className="rounded-2xl border shadow-x1 p-8 max-w-lg">
        <div className="flex flex-col items-center space-y-4">
          <h1 className="font-bold text-2xl text-indigo-700 w-4/6 text-center">
            {props.title}
          </h1>
          <p className="text-sm text-indigo-500 text-center w-5/6">
            {props.description}
          </p>
          <input
            type="text"
            input={input}
            onChange={handleChange}
            placeholder={props.placeholder}
            className="border-2 text-indigo-500 rounded-lg w-full h-12 px-4"
          />
          <button
            className="bg-indigo-500 text-white rounded-md hover:bg-indigo-600 font-semibold px-4 py-3 w-full" onClick={handleSubmit}
          >
            {props.btn_text}
          </button>
        </div>
      </div>
    </div>
    
  )
}
